/* 폼 요소 스타일 */
form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 80%;
    max-width: 600px;
}

label {
    margin-bottom: 5px;
}

input[type="text"],
textarea {
    width: calc(100% - 20px);
    padding: 8px;
    margin-bottom: 10px;
}

input[type="submit"] {
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
    align-self: flex-end;
}

input[type="submit"]:hover {
    background-color: #0056b3;
}

.back-button {
    padding: 10px 20px;
    background-color: #ddd;
    color: #000;
    border: 1px solid #888;
    border-radius: 5px;
    text-decoration: none;
    margin-bottom: 10px;
}

.back-button:hover {
    background-color: #ccc;
}
